import React, {useEffect, useState} from "react";
import {useHistory, useParams} from 'react-router-dom';
import  {Card,Button} from 'react-bootstrap';
import styled from 'styled-components';
import './Product.scss';
/* styled-components           . */
let Box = styled.div`
  padding : 20px;
`;

let Subject = styled.h4`
    font-size:14px;
    color: ${props => props.coloring};
    line-height:180%;
`;
/* ${}   ``    ES6,        .
        props.    ,     .
      css   .  SASS  CSS+SASS   CSS IMPORT         .
*/

function Product(props){
    let history = useHistory();
    let {id} = useParams();
    var srcUrl = "../flower"+[id]+".jpg";

    let[alert, changeAlert] = useState(true);
    let[inputData, changeInputData] = useState('');
    let findProduct = props.flower.find(function(product){
        return product.id == id
        /*
          =      idx()     ? 
        find()  . array        . 
           (product)  , array     . 
        return     .      . 
           URL /:id  (.id)    .
        
           id:0  Ajax      .
        */
    });

    useEffect(()=>{
    /* useEffect  Lifecycle Hook     .  ,  .   useEffect import   .
                . 
    -       (mount  )
    -     (update   )
    */
        // setTimeout(()=>{
        //     document.getElementById('alertBox').style.display = 'none';
        // },2000);

        let timer = setTimeout(()=>{
            changeAlert(false)
        },2000);

        return function ex(){
            /* useEffect  return   ,       . ()=>   */
            clearTimeout(timer);
            //      .(      .)
        }

    },[alert]);
     /*  [] state , alert  state        .
      input onChange        useEffect   . 
        1.    , 2. alert state   . 
       state     . 
     []     ? =>     ,    . 
     */
    

    // useEffect(()=>{
    //     // 2  .
    //     // useEffect    ,    .
    // });

    return(
        <Card className="text-center">
        <div className="productImg" >
        <Card.Img variant="top" src={srcUrl} />
        </div>
        <Card.Header>{findProduct.title}</Card.Header>
        <Card.Body>
            {/* useParams()  URL    1, 2   . 
             destructuring      .
             id  :id   .  */}
            

            <Box>
                <Card.Text>
                    <Subject coloring={'dimgrey'}>{findProduct.content}</Subject>
                    {/*   props.coloring    . */}                
                </Card.Text>
                {inputData} 
                <Info stock={props.stock} num={id}></Info>
                {/*
                <input onChange={(e)=>{changeInputData(e.target.value)}} /> 
                */}
                <Button onClick={()=>{history.push('/detail')}} variant="primary"></Button>
                <Button onClick={()=>{
                    let exList = [...props.stock];
                    exList[id] = props.stock[id]-1;      
                    props.changeStock(exList)}}></Button>
                
                { 
                    alert === true ? (
                        <div id="alertBox" className="my-alert">
                            <p>   </p>
                        </div>
                    ) : null
                }
            </Box>
        </Card.Body>
        <Card.Footer className="text-muted">{id} 2 days ago</Card.Footer>
        </Card>        


    )
}

function Info(props){
    return(
        <p>: {props.stock[props.num]}</p>
    )
}
export default Product