import React from "react";
import {useHistory} from 'react-router-dom';
import  {Card, Link, Row, Col} from 'react-bootstrap';
import './Detail.scss';
function Detail(props){
    let history = useHistory();
    return(
        <Col>
        <Card>
          <Card.Link href={'/detail/'+props.i}><Card.Img variant="top" src={require('./flower'+(props.i)+'.jpg')} /></Card.Link>
          <Card.Body>
            <Card.Title>{props.flower.title}</Card.Title>
            <Card.Text>
              {props.flower.content}
            </Card.Text>
          </Card.Body>
          <Card.Footer>
            <small className="text-muted">{props.flower.price}</small>
            <button className="baseBtn red"></button> 
            <button onClick={()=>{history.goBack()}} className="baseBtn"></button> 
            {/*      history.push('/')  . */}
          </Card.Footer>
        </Card>
        </Col>

  )
}

export default Detail